:root{
    --PrimaryColor: hsl(94,59%,35%);
    --HoverColor: #bdf094;
    --paleGreen: hsl(96,75%,89%);
    --whiteColor: hsl(0,0%,100%);
    --blackColor: hsl(0,0%,18%);
    --textColor: hsl(240,1%,48%);
    --bgColor: hsl(220,10%,94%);
    --greyText: rgb(190,190,190);
    --inputColor: hsl(330,12%,97%);
    --itemCardColor: hsl(120,5%,88%);
    --itemCardColor: hsl(120,27%,87%);
  
    --biggestFontSize: 2.5rem;
    --h1FontSize: 1.5rem;  
    --h2FontSize: 1.25rem;
    --h3FontSize: 1rem;
    --normalFontSize: .938rem;
    --smallFontSize: .813rem;
    --smallestFontSize: .75rem;
  }

  .ListingSection{
    flex-basis: 70%;

    .Heading{
        width: 100%;
        justify-content: space-between;

        h1,h3{
            font-size: var(--h2FontSize);
            color: var(--blackColor);
            font-weight: 700;
            text-align: center;
        }

        .btn{
            margin-top: 4px;
            border: 1px solid transparent;
            box-shadow: 0.02rem .06rem;
        }
        .btn:hover{
            border: 1px solid var(--PrimaryColor);
        }
    }

    .SecContainer{
        width: 100%;
        margin: 1rem 0;
        gap: 1rem;
        flex-wrap: wrap;
        justify-content: space-between;

        .SingleItem{
            position: relative;
            width: 150px;
            height: 170px;
            display: flex;
            align-items: center;
            flex-direction: column;
            padding: 1rem;
            border-radius: 10px;
            // background: var(--itemCardColor);
            background: var(--whiteColor);
            

            &:hover{
                // background: var(--itemCardHover);
                background: var(--itemCardColor);
                box-shadow: 0 2px 2px var(--inputColor);
            }
            img{
                max-width: 100px;
            }
            .icon{
                position: absolute;
                top: 10px;
                right: 10px;
                color: var(--PrimaryColor);

                // &:hover{
                //     color: var(--PrimaryColor);
                // }
                // &:active{
                //     color: var(--primaryColor);
                // }
            }
            h3{
                font-size: var(--h3FontSize);
                color: var(--blackColor);
                padding: 1rem 0;
            }
        }
    }

    .Sellers{
        width: 100%;
        justify-content: space-between;
        gap: 1rem;
        align-items: flex-start;

        .TopSellers,.FeaturedSellers{
            .Heading{
                padding: 1rem 0;

                .btn{
                    padding: .5rem 1rem;
                    box-shadow: 0.02rem .06rem;
                }
            }
            .Card{
                padding: 1rem;
                border-radius:1rem;
                background: var(--whiteColor);
                gap: 1rem;
                box-shadow: 0 2px 4px var(--inputColor);

                .Users{
                    padding-right: 1rem;
                    border-right: 2px solid var(--greyText);

                    img{
                        width:30px;
                        height: 30px;
                        border-radius: 50%;
                        object-fit: cover;
                        border: 2px solid var(--inputColor);
                        transition: .5s ease;

                        &:hover{
                            transform: translateY(-10px);
                        }
                    }
                }

                .CardText{
                    span{
                        color: var(--blackColor);
                        font-weight: 500;

                        small{
                            font-weight: 400;
                            .date{
                                margin-bottom: 1rem;
                            }
                        }
                    }
                }
            }
        }
    }
  }

@media screen and (max-width: 990px){
    .ListingSection{
        .Sellers{
            .Card{
                flex-direction: column;
            }
        }
    }
}

@media screen and (max-width: 918px){
    .ListingSection{
        .SingleItem{
            width: 200px !important;
        }
    }
}

@media screen and (max-width: 490px){
    .ListingSection{
        .Sellers{
            flex-direction: column;
            .Card{
                flex-direction: column;
            }
        }
    }
}